<template>
  <div class="login" >
    <p>hello，我是Chat Bear，你叫什么名字呢？</p>
    <el-input v-model="userName" placeholder="请输入名字"></el-input>
    <el-button type="success" @click="confirm">确定</el-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userName: ""
    };
  },
  mounted () {
    //从cookies取得用户名
    this.userName=this.$cookies.get("userName");
    if(!this.userName){
        console.log("用户名为空")
    }else {
      this.$router.push({
        path: `/chatting`
      });
    }
  },
  methods: {
    confirm() {
      //将用户名存在cookies，跳转页面
      if(!this.userName){
          this.$message.success("你还没有告诉我你的名字哦");
      }else{
        this.$cookies.set("userName", this.userName);
        this.$router.push({
        path: `/chatting`
      });
      }
      
    }
  }
};
</script>
