<template>
  <div class="chatWindow">
    <p>ChatBear</p>
    <ul id="chatList">
      <li v-for="item in messageData" :key="item">{{ item }}</li>
    </ul>
    <div id ="input">
    <el-form ref="form" :model="form" @submit.native.prevent>
      <el-form-item>
        <el-input v-model="form.question" @keyup.enter.native="onSubmit"></el-input>
        <el-button type="primary" @click="onSubmit">发送</el-button>
      </el-form-item>
    </el-form>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from "axios";
import { ksort } from "../plugins/ksort.js";
import md5 from "js-md5";
export default {
  data() {
    return {
      userName: "",
      robotName: "Bear",
      messageData: [],
      responseMessage: "",
      form: {
        question: ""
      },
      uploadUrl:"/backapi",
      requestUrl: "https://api.ai.qq.com/fcgi-bin/nlp/nlp_textchat",
      appID: 2121801441,
      appKey: "ZCOSN0mwkST3q0KB",
      requestParams: {
        app_id: 0,
        time_stamp: 0,
        nonce_str: "",
        sign: "",
        session: "",
        question: ""
      },
      uploadData:{
          sessionId:'',
          userName:'',
          receiveMessage:'',
          responseMessage:''
      }
    };
  },
  mounted () {
    //从cookies取得用户名
    this.userName=this.$cookies.get("userName");
    if(!this.userName){
        console.log("用户名为空")
    }else {
      this.messageData.push(this.robotName+':Hi,'+this.userName+',你好呀!');
      this.uploadData.userName=this.userName;
    }
  },
  methods: {
    //生成随机字符串
    randomString(length) {
      var chars =
        "0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ";
      var result = "";
      for (var i = length; i > 0; --i)
        result += chars[Math.floor(Math.random() * chars.length)];
      return result;
    },
    jsToFormData(obj) {
      //对象转formdata格式
      let formData = new FormData();
      Object.keys(obj).forEach(function(key) {
        formData.append(key, obj[key]);
        //console.log(key, obj[key]);
      });
      return formData;
    },
    //生成签名
    getSign(params, appKey) {
      //先置空sign否则报错
      params.sign = "";
      //排序
      let sortedParams = ksort(this, params);
      //console.log(sortedParams);
      //拼接URL键值对
      let bb = "";
      Object.keys(sortedParams).forEach((key, i) => {
        if (sortedParams[key] !== "") {
          bb += key + "=" + encodeURIComponent(sortedParams[key]) + "&";
        }
      });
      //拼接appKey
      let cc = `${bb}app_key=${appKey}`;

      // MD5运算
      let dd = md5(cc);
      //转换大写
      let sign = dd.toUpperCase();
      //console.log("getSign:" + sign);
      return sign;
    },
    onSubmit() {
      this.messageData.push(this.userName+':'+this.form.question);
      this.uploadData.receiveMessage=this.form.question;
      //console.log(this.form.question);
      this.request();
      this.form.question='';
    },
    uploadMessage(){
      //部署在GitHub page，不需要上传数据
        // let uploadFormData = this.jsToFormData(this.uploadData);
        // axios.post(this.uploadUrl, uploadFormData).then(res => {
        //   //请求成功
        //   if (res== "success") {
        //     console.log("上传message成功");
        //     //请求失败
        //   } else {
        //     //console.log(res);
        //   }
        // });

    },
    request() {
      if (this.form.question == "") {
        this.$message.warning(`请输入`);
      } else {
        this.requestParams.app_id = this.appID;
        //获取时间戳
        //var myDate = new Date();
        this.requestParams.time_stamp = Date.parse(new Date()) / 1000;
        //console.log("时间戳为：" + this.requestParams.time_stamp);
        //生成nonce_str
        this.requestParams.nonce_str = this.randomString(32);
        //设置session
        this.requestParams.session = this.requestParams.time_stamp;
        //设置问题
        this.requestParams.question = this.form.question;
        //生成签名
        this.requestParams.sign = this.getSign(this.requestParams, this.appKey);
        //console.log("the sign is:" + this.requestParams.sign);
        //提交请求
        let submitFormData = this.jsToFormData(this.requestParams);
        //console.log("sign in FormData :" + submitFormData.get("sign"));
        axios.post(this.requestUrl, submitFormData).then(res => {
          //请求成功
          if (res.data.ret == 0) {
            this.responseMessage = res.data.data.answer;
            this.uploadData.responseMessage=this.responseMessage;
            // this.$message({
            //   message:this.responseMessage,
            //   type: "success"
            // });
            this.messageData.push(this.robotName+":"+this.responseMessage);
            this.uploadMessage();
            //请求失败
          } else {
            this.$message.warning("error");
            //console.log(res);
          }
        });
      }
    }
  }
};
</script>

<style>
#input {
  text-align: right;

}
</style>